export default WazirxCloneScript;

export const queryData = graphql`
  query MyQuery {
    wazirxClongImg: file(relativePath: { eq: "wazrix-clone.png" }) {
      childImageSharp {
        gatsbyImageData
      }
    }
    site {
      siteMetadata {
        firestore {
          apiKey
          appId
          authDomain
          messagingSenderId
          projectId
          storageBucket
        }
      }
    }
    allMetaJson(
      filter: { slug: { eq: "explore-the-features-of-wazirx-clone-script" } }
    ) {
      nodes {
        metaTitle
        metaKeyword
        title
        slug
        description
      }
    }
  }
`;
